import React from 'react';
import APILayout from '../../../../layouts/APILayout';
import Section from '../../../../modules/apidoc/components/Section';
import FunctionName from '../../../../modules/apidoc/components/FunctionName';
import ParameterTable from '../../../../modules/apidoc/components/ParameterTable';
import ParameterGroup from '../../../../modules/apidoc/components/ParameterGroup';
import Parameter from '../../../../modules/apidoc/components/Parameter';
import { StyleType } from '../../../../modules/apidoc/components/OpenlayersDataTypes';
import SectionSubtitle from '../../../../modules/apidoc/components/SectionSubtitle';

export default () => (
    <APILayout>
        <h4 className='font-weight-bold mb-4'>easyolmaps/style/Style</h4>
        <Section>
            <FunctionName parameters={['options']}>new Style</FunctionName>
            <ParameterTable>
                <ParameterGroup name='options' type='Style Options'>
                    <ParameterTable name='options' withDefaultColumn={true}>
                        <Parameter name='fill' type='boolean' defaultValue=' '>Set to false if no fill is desired.</Parameter>
                        <Parameter name='fillColor' type='string' defaultValue="'#EE9900'">Hex fill color.</Parameter>
                        <Parameter name='fillOpacity' type='number' defaultValue='0.4'>Fill opacity (0-1).</Parameter>
                        <Parameter name='stroke' type='boolean' defaultValue=' '>Set to false if no stroke is desired.</Parameter>
                        <Parameter name='strokeColor' type='string' defaultValue="'#EE9900'">Hex stroke color.</Parameter>
                        <Parameter name='strokeOpacity' type='number' defaultValue='0.4'>Stroke opacity (0-1).</Parameter>
                        <Parameter name='strokeWidth' type='number' defaultValue='1'>Pixel stroke width.</Parameter>
                        <Parameter name='strokeLinecap' type='string' defaultValue="'round'">Stroke cap type.</Parameter>
                        <Parameter name='strokeDashstyle' type='string' defaultValue="'solid'">Stroke dash style.</Parameter>
                        <Parameter name='graphic' type='boolean' defaultValue=' '>Set to false if no graphic is desired.</Parameter>
                        <Parameter name='pointRadius' type='number' defaultValue='6'>Pixel point radius.</Parameter>
                        <Parameter name='externalGraphic' type='string' defaultValue=' '>Url to an external graphic that will be used for rendering points.</Parameter>
                        <Parameter name='graphicOpacity' type='number' defaultValue='1'>Opacity (0-1) for an external graphic.</Parameter>
                        <Parameter name='rotation' type='number' defaultValue=' '>Rotation in radians (positive rotation clockwise).</Parameter>
                        <Parameter name='anchor' type='Array.<number>' defaultValue='[0.5,0.5]'>Anchor. Default value is the icon center.</Parameter>
                        <Parameter name='label' type='string' defaultValue=' '>The text for an optional label.</Parameter>
                        <Parameter name='labelAlign' type='string' defaultValue="'center'">Label alignment. Possible values: 'left', 'right', 'center', 'end' or 'start'.</Parameter>
                        <Parameter name='labelBaseline' type='string' defaultValue="'middle'">Label base line. Possible values: 'bottom', 'top', 'middle', 'alphabetic', 'hanging', 'ideographic'.</Parameter>
                        <Parameter name='labelXOffset' type='number' defaultValue='0'>Horizontal text offset in pixels. A positive will shift the text right.</Parameter>
                        <Parameter name='labelYOffset' type='number' defaultValue='0'>Vertical text offset in pixels. A positive will shift the text down.</Parameter>
                        <Parameter name='labelOutlineColor' type='string' defaultValue="'#FFFFFF'">The color of the label outline.</Parameter>
                        <Parameter name='labelOutlineWidth' type='number' defaultValue='3'>The width of the label outline.</Parameter>
                        <Parameter name='labelOutlineOpacity' type='number' defaultValue='1'>The opacity (0-1) of the label outline.</Parameter>
                        <Parameter name='fontColor' type='string' defaultValue="'#000000'">The font color for the label,to be provided like in CSS.</Parameter>
                        <Parameter name='fontOpacity' type='number' defaultValue='1'>Opacity (0-1) for the label.</Parameter>
                        <Parameter name='fontFamily' type='string' defaultValue="'sans-serif'">The font family for the label,to be provided like in CSS.</Parameter>
                        <Parameter name='fontSize' type='string' defaultValue="'11px'">The font size for the label,to be provided like in CSS.</Parameter>
                        <Parameter name='fontStyle' type='string' defaultValue="'normal'">The font style for the label, to be provided like in CSS.</Parameter>
                        <Parameter name='fontWeight' type='string' defaultValue="'normal'">The font weight for the label, to be provided like in CSS.</Parameter>
                    </ParameterTable>
                </ParameterGroup>
            </ParameterTable>
            <SectionSubtitle><strong>Extends</strong></SectionSubtitle>
            <ul>
                <li><StyleType /></li>
            </ul>
        </Section>
    </APILayout>
);